export default {
  name: 'Uploader',
  inject: ['$builder', '$section'],
  props: {
    path: {
      type: String,
      required: true
    },
    mode: {
      default: 'input',
      type: String
    }
  },
  data: function data() {
    return {
      src: ''
    };
  },
  created: function created() {
    this.src = this.$section.get(this.path);
  },
  methods: {
    updateImage: function updateImage() {
      var file = this.$refs.uploader.files[0];
      if (!file) return;
      var imageURL = URL.createObjectURL(file);
      this.src = imageURL;
      this.$section.set(this.path, imageURL);
    }
  }
};